import React from 'react';

export default function ProjectCard({ project }) {
  return (
    <a className='screenshot-card' href={project.url} target='_blank' rel='noopener noreferrer'>
      <div className='screenshot-card'>
        <div className='screenshot-image-div'>
          <img src={project.image} alt='SS' className='project-card-image'></img>
        </div>
      </div>
      <div className='screenshot-detail-div'>
        <h5 className='project-card-title'>{project.title}</h5>
        <p className='project-card-subtitle'>{project.description}</p>
      </div>
    </a>
  );
}
