import React from 'react';
import './Experience.css';
import ExperienceCard from '../../components/experienceCard/ExperienceCard';
import { expSection } from '../../portfolio';
import { Fade } from 'react-reveal';

export default function Experience() {
  return (
    <Fade bottom duration={1000} distance='20px'>
      <div className='main' id='experience'>
        <div className='experience-header'>
          <h1 className='experience-header-text'>{expSection.title}</h1>
          <p className='subTitle experience-subtitle'>{expSection.subtitle}</p>
        </div>
        <div className='experience-text-div'>
          {expSection.experience.map((experience) => {
            return (
              <ExperienceCard
                experience={{
                  title: experience.title,
                  description: experience.description,
                }}
              />
            );
          })}
        </div>
      </div>
    </Fade>
  );
}
